.description-of-goods {
  position: relative;

  margin: 0;
  padding: 230px 0 275px 0;

  z-index: 5;

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    padding: 113px 62px 161px 62px;
  }

  @media (max-width: $tablet-width) {
    padding: 226px 0 70px 0;
  }
}

.description-of-goods__name {
  display: flex;
  flex-direction: column;

  width: 720px;

  &--header {
    font-family: $font--roboto-bold;
    font-style: normal;
    font-weight: bold;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;

    text-transform: uppercase;

    color: $white;
  }

  &--text {
    position: relative;

    font-family: $font--pt-mono;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 34px;

    margin: 27px 0 44px 0;
    padding-left: 97px;

    color: $white;
  }

  &--text::before {
    position: absolute;
    top: 17px;
    left: 0;

    width: 66px;
    height: 2px;

    content: "";

    background: $white;
  }

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    width: 470px;

    &--header {
      font-size: 60px;
      line-height: 70px;
    }

    &--text {
      font-size: 18px;
      line-height: 28px;

      margin: 30px 0 54px 0;
      padding-left: 53px;
    }

    &--text::before {
      top: 15px;
      left: 3px;

      width: 33px;
    }
  }

  @media (max-width: $tablet-width) {
    width: 277px;

    &--header {
      font-size: 40px;
      line-height: 47px;
    }

    &--text {
      font-size: 16px;
      line-height: 25px;

      margin: 28px 0 39px 0;
      padding-left: 48px;
    }

    &--text::before {
      top: 15px;

      width: 33px;
    }
  }
}

.description-of-goods__feedback {
  font-size: 16px;
  line-height: 19px;

  margin: 0 0 0 97px;
  padding: 17px 43px 16px 43px;

  border: 2px solid $blue;
  border-radius: 5px;

  color: $white;
  background-color: $blue;

  &--tablet {
    display: block;
  }

  &--mobil {
    display: none;
  }

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    margin: 0 0 0 52px;
    padding: 17px 29px 16px 29px;
  }

  @media (max-width: $tablet-width) {
    &--tablet {
      display: none;
    }

    &--mobil {
      display: block;

      margin: 0;
      padding: 16px 30px 16px 29px;
    }
  }
}

.description-of-goods__scroll-down {
  position: absolute;
  bottom: 128px;
  left: -35px;

  font-family: $font--pt-mono;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;

  color: $white;

  opacity: 0.5;

  transform: rotate(-90deg);

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    display: none;
  }

  @media (max-width: $tablet-width) {
    display: none;
  }
}
